import { Box, Button, ButtonGroup, Grid, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';
import * as React from 'react';

import { PRODUCTS_MAP, ShoppingCartItem } from '../models';

const ItemsListWrapper = styled(Box)(() => ({
  padding: 20,
}));

type ItemsListProps = {
  items: ShoppingCartItem[];
  handlerQuantity: (id: string, quantity: number) => void;
  deletePruduct: (id: string) => void;
};

const ItemsList: React.FC<ItemsListProps> = ({
  items,
  handlerQuantity,
  deletePruduct,
}) => {
  return (
    <ItemsListWrapper>
      {items.map((item) => {
        const product = PRODUCTS_MAP[item.productId];
        const price = product?.price || 0;
        const incQuantity = () => {
          handlerQuantity(product.id, item.quantity + 1);
        };
        const decQuantity = () => {
          handlerQuantity(product.id, item.quantity - 1);
        };

        return (
          <Grid container key={item.productId}>
            <Grid item xs={12}>
              <Typography>{product?.label}</Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography>{`${item.quantity} x $${price} = $${item.quantity * price
                }`}</Typography>
            </Grid>
            <ButtonGroup variant="outlined" aria-label="outlined button group">
              <Button onClick={incQuantity}>+</Button>
              <Button onClick={decQuantity}>-</Button>
              <Button
                onClick={() => {
                  deletePruduct(product.id);
                }}
              >
                x
              </Button>
            </ButtonGroup>
          </Grid>
        );
      })}
    </ItemsListWrapper>
  );
};

export default ItemsList;
